import React from 'react';
import Projects from './Projects';
import Aos from 'aos';
import Skrollr from 'skrollr';
import { BgOverlay, Banner, Profile, ScrollDownBtn } from './Header';
import { SepHighlight, PopUpScreen } from "./DomHelper";
import BackToTop from "react-back-to-top-button";


/*******************************TODO
-put to host
- image gathering
-update text
-update image
-update link
-loading bar based on scroll

**********************************/

//=== third party script init ===
Aos.init();
let s = Skrollr.init({
    forceHeight: false,
    mobileCheck: function () {
        //hack - forces mobile version to be off
        return false;
    }
});
//

class App extends React.Component {
    //equivalent to commented block below
    state = {
        isColorChange: false,
        currentBreakpoint: window.innerWidth,
        projectContent: null,
        activeState: false
    }

    //=== References ===
    midDiv = React.createRef();
    popUp = React.createRef();
    //

    componentDidMount() {
        window.scrollTo(0, 0);
        s.refresh(); //reinit skrollr after mount
        window.addEventListener('scroll', this.scrollUpBtnColor);
        window.addEventListener("resize", this.updateBreakPoint);
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.scrollUpBtnColor);
        window.removeEventListener("resize", this.updateBreakPoint);

    }

    updateBreakPoint = () => {
        this.setState({
            currentBreakpoint: window.innerWidth
        },
            () => {
                s.refresh();
            });
    }

    scrollUpBtnColor = () => {
        let documentHeight = Math.max(document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight);
        let triggerPoint = documentHeight - (this.state.currentBreakpoint <= 769 ? (this.state.currentBreakpoint <= 480 ? 388 : 347) : 515) + (window.innerHeight * 0.1) + (this.state.currentBreakpoint <= 874 ? 30 : 50);
        if ((window.innerHeight + document.documentElement.scrollTop) >= triggerPoint) {
            this.setState({
                isColorChange: true
            });
        }
        else {
            this.setState({
                isColorChange: false
            });
        }

    };

    popupContent = (projectContent) => {
        this.setState({
            projectContent: projectContent,
            activeState: true
        })
        //
        this.popUp.current.style.top = document.documentElement.scrollTop + 100 + "px";
    }

    closePopUp = () => {
        this.setState({
            activeState: false
        });
        setTimeout(() => {
            this.setState({
                projectContent: null
            });
            this.popUp.current.style.top = "0px";

        }, 1500);
    }

    toggleProject = (content) => {
        this.setState({
            activeState: false
        });
        setTimeout(() => {
            this.setState({
                activeState: true,
                projectContent: content
            });
            this.popUp.current.style.top = document.documentElement.scrollTop + 100 + "px";
        }, 1500);
        
    }

    render() {
        return (
            <div id="abt-jellymiso"
                data-0={this.state.currentBreakpoint <= 1440 ?
                    (this.state.currentBreakpoint <= 769 ? null
                        : "padding-top:550px")
                    : "padding-top:750px"
                }
                data-275={this.state.currentBreakpoint <= 769 ? "padding-top:400px" : null}
                data-450={this.state.currentBreakpoint <= 1440 ?
                    (this.state.currentBreakpoint <= 769 ? null
                        : "padding-top:550px")
                    : "padding-top:750px"
                }
            >

                <div id="topDiv"
                    data-0={this.state.currentBreakpoint <= 1440 ?
                        (this.state.currentBreakpoint <= 769 ? "height:400px"
                            : "height:550px")
                        : "height:750px"
                    }

                    data-275={this.state.currentBreakpoint <= 769 ? "border-bottom:4px solid rgba(255, 97, 127, 0);height:125px;" : null}
                    data-450={this.state.currentBreakpoint <= 1440 ?
                        (this.state.currentBreakpoint <= 769 ? null
                            : "border-bottom:4px solid rgba(255, 97, 127, 0);height:66px;")
                        : "border-bottom:4px solid rgba(255, 97, 127, 0);height:300px;"
                    }

                    data-400={this.state.currentBreakpoint <= 769 ?
                        (this.state.currentBreakpoint <= 480 ? "border-bottom:4px solid rgba(255, 97, 127, 1);height:50px"
                            : "border-bottom:4px solid rgba(255, 97, 127, 1);height:66px")
                        : null
                    }
                    data-684={this.state.currentBreakpoint <= 769 ? null : "border-bottom:4px solid rgba(255, 97, 127, 1);height:66px"}
                >
                    <div className="logo" data-aos="fade-right" data-aos-delay="1200" data-aos-anchor-placement="top-right"
                        data-175={this.state.currentBreakpoint <= 769 ? "top:30px;" : null}
                        data-350={this.state.currentBreakpoint <= 769 ? null : "top:30px;"}

                        data-215={this.state.currentBreakpoint <= 769 ?
                            (this.state.currentBreakpoint <= 769 ? "top:12px"
                                : "top:15px;")
                            : null
                        }
                        data-400={this.state.currentBreakpoint <= 769 ? null : "top:15px;"}
                    >
                        <img src="/images/logo.png" alt="Logo"

                            data-150={this.state.currentBreakpoint <= 769 ?
                                (this.state.currentBreakpoint <= 480 ? "height:31px;" : "height:45px;")
                                : null
                            }
                            data-175={this.state.currentBreakpoint <= 769 ?
                                (this.state.currentBreakpoint <= 480 ? "height:23px;" : "height:31px;")
                                : null
                            }
                            data-305={this.state.currentBreakpoint <= 1440 ?
                                (this.state.currentBreakpoint <= 769 ? null
                                    : "height:45px")
                                : "height:62px"
                            }
                            data-350={this.state.currentBreakpoint <= 769 ? null : "height:31px;"}
                        />
                    </div>
                    <Profile curBreakpoint={this.state.currentBreakpoint} />
                    <ScrollDownBtn curBreakpoint={this.state.currentBreakpoint} el={this.midDiv} />
                    <BgOverlay />
                    <Banner />


                </div>
                <div id="skrollr-body">
                <div id="midDiv" ref={this.midDiv} data-aos="fade-in" data-aos-once="true" data-aos-duration="750" >

                    <Projects projectOnClick={this.popupContent} />
                </div>
                <div id="botDiv">
                    <div className="findMe">
                        <h2>Contact Me</h2>
                        <span className="msgBody">
                            <a className="alt" href="mailto:jeremysohwk92@gmail.com" target="_blank" rel="noopener noreferrer">jeremysohwk92{SepHighlight("@")}gmail{SepHighlight(".")}com</a>
                        </span>
                    </div>
                    <div className="specialThanks">
                        <h3>Special Thanks</h3>
                        <span className="msgBody">
                            <a className="alt" href="https://pages.github.com/" target="_blank" rel="noopener noreferrer">GitHub Pages</a>
                            {SepHighlight(" / ")}
                            <a className="alt" href="https://unsplash.com/" target="_blank" rel="noopener noreferrer">Unsplash</a>
                            {SepHighlight(" / ")}
                            <a className="alt" href="https://www.flaticon.com/" target="_blank" rel="noopener noreferrer">flaticon</a>
                            {SepHighlight(" / ")}
                            <a className="alt" href="https://icons8.com/" target="_blank" rel="noopener noreferrer">icons8</a>
                        </span>
                    </div>
                    <BgOverlay />
                    <div className="footerDiv">
                        <div className="foot-item reactSol">
                            made with <img src="/images/react.png" alt="reactJs" height="16px" style={{ verticalAlign: "-3px" }} /> React
                        </div>
                        <div className="foot-item designBy">
                            <span className="foot-item">designed {SepHighlight('&')} developed by</span> <img src="/images/logo.png" alt="jellymiso" height="31px" />
                        </div>
                    </div>
                </div>

                {
                    <BackToTop showAt={1000} speed={2500} easing="easeInOutQuint">
                        <div className="scrollTopBtn" style={{ filter: (this.state.isColorChange ? "grayscale(100%) brightness(1000%) " : "none") }}>
                            <img src="images/up-arrow.png" alt="Back to top" />
                        </div>
                    </BackToTop>
                }
                {
                    <PopUpScreen childRef={this.popUp} activeState={this.state.activeState} content={this.state.projectContent} closeBtnClick={this.closePopUp} toggleProjectClick={this.toggleProject} />
                }
                </div>
            </div>
        )
    }
}

export default App;