import React from 'react';
import Socials from './Socials';
import { SepHighlight } from "./DomHelper";


/* Scroll Down Button */
export class ScrollDownBtn extends React.Component {

    scrollDown = (event, ref) => {
        //console.log(this.refs, ref)
        const offset = 33;
        const el = ref.current;
        const adjustedOffset = el.offsetTop - offset
        window.scrollTo({ top: adjustedOffset, behavior: 'smooth' })
        //this.scrollToExtend(adjustedOffset, 100)
    }

    render() {
        return (
            <span className="scrollDown" onClick={e => this.scrollDown(e, this.props.el)} data-aos="flip-down" data-aos-delay="1500"    
                data-174={this.props.curBreakpoint <= 769 ? "transition:;" : null}
                data-175={this.props.curBreakpoint <= 769 ? "opacity:1;transition:none;" : null}
                data-349={this.props.curBreakpoint <= 769 ? null : "transition:;" }
                data-350={this.props.curBreakpoint <= 769 ? null : "opacity:1;transition:none;"}
                
                data-315={this.props.curBreakpoint <= 769 ? "opacity:0;transition:none;" : null}
                data-550={this.props.curBreakpoint <= 769 ? null : "opacity:0;transition:none;"}
            >
                <img src="images/down-arrow.png" alt="scroll down" />
                <img className="hover" src="images/down-arrow-hover.png" alt="scroll down" />
            </span>
        )
    }
}


/* Background Overlay */
export class BgOverlay extends React.Component {
    render() {
        return (
            <div className="overlay"></div>
        )
    }
}


/* Banner & Bg Elements */
export class Banner extends React.Component {
    state = { offset: 0 }

    componentDidMount() {
        window.addEventListener('scroll', this.parallaxShift);
    }
    componentWillUnmount() {
        window.removeEventListener('scroll', this.parallaxShift);
    }
    parallaxShift = () => {
        this.setState({
            offset: document.documentElement.scrollTop
        });
    };

    render() {
        return (
            <div className="banner">
                <div className="bpart1" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="1750"></div>
                <div className="bpart2" data-aos="fade-left" data-aos-duration="1000" data-aos-delay="1750" style={{ backgroundPositionY: this.state.offset / 15 }}></div>
                <div className="bpart3" data-aos="fade-up" data-aos-duration="1000" data-aos-delay="1750" style={{ backgroundPositionY: this.state.offset / 8 }}></div>
                <div className="bpart4" data-aos="zoom-out" data-aos-duration="1000" data-aos-delay="1750" style={{ backgroundPositionY: this.state.offset / 10 }}></div>
                <div className="bpart5" data-aos="zoom-in" data-aos-duration="1000" data-aos-delay="1750" style={{ backgroundPositionY: this.state.offset / 5 }}></div>
                <div className="bpart6" data-aos="fade-right" data-aos-duration="1000" data-aos-delay="1750"></div>
                <div className="bg"></div>
             </div>

        )
    }
 }



/* BG Profile & Bio */
