import React from 'react';
import TAGS from './data/tags';

class Tag extends React.Component {


    render() {

        const { name, subcat } = this.props.content;

        return (
            <span className={(this.props.isActive ? "sel " : "") + "tag-item [tag-subcat-code]"} data-id={this.props.index} data-subcat={subcat} onClick={() => this.props.onClick(this.props.index)} >{name}</span>
        )
    }
}

class Tags extends React.Component {



    render() {
        return (
            <div className="tags" id="tagList">
                <span 
                    data-id="-1"
                    className={(this.props.activeTagItem === -1 ? "sel " : "") + "tag-item [tag-subcat-code]"} 
                    onClick={() => { this.props.tagOnClick(-1)}}
                >All</span>
                {
                    TAGS.map(TAG => {
                        return (
                            <Tag key={TAG.id} content={TAG} onClick={this.props.tagOnClick} index={TAG.id} isActive={this.props.activeTagItem === TAG.id ? true : false}  />
                        );
                    })
                }
            </div>
        )
    }
}

export default Tags;