import React from 'react';
import { BgOverlay } from './Header';
import PROJECTS from './data/projects';
import ReactTooltip from 'react-tooltip'; 

//=== Constants ===
export const SepHighlight = (textContent) => <span className="sep">{textContent}</span>;
//

export class FullPageOverlay extends React.Component{

    render(){
        return(
            <div className="full-overlay"></div>
        )
    }

}

export class PopUpScreen extends React.Component{
    

    render(){

        let sortedArray = PROJECTS.sort((a, b) => a.title.localeCompare(b.title));
        let currentIndex = this.props.content === null ? -1 : sortedArray.findIndex(a => a.id === this.props.content.id);
        let prevProjContent = this.props.content === null ? null : 
            (currentIndex > 0 ? sortedArray[currentIndex - 1] : sortedArray[sortedArray.length -1] )
        let nextProjContent = this.props.content === null ? null :
            (currentIndex < sortedArray.length - 1 ? sortedArray[currentIndex + 1] : sortedArray[0]);

        return(
  
            <div className={"popupScreen" + (this.props.activeState ? " active" : "")}>
                <div className="misc">
                    <span className="prev project" onClick={() => this.props.toggleProjectClick(prevProjContent)} data-tip={"PREVIOUS PROJECT: " + (prevProjContent != null && prevProjContent.title)}>
                        <img src="images/prev.png" /> Prev<span className="mobile-hide"> Project</span>
                    </span>
                    <span className="closeBtn" onClick={() => this.props.closeBtnClick()}>&times; Close<span className="mobile-hide"> Popup</span></span>
                    <span className="next project" onClick={() => this.props.toggleProjectClick(nextProjContent)} data-tip={"NEXT PROJECT: " + (nextProjContent != null && nextProjContent.title)}>
                        Next <span className="mobile-hide">Project </span><img src="images/next.png" />
                    </span>
                    <ReactTooltip place="bottom" type="light" effect="float" className="tip" />
                </div>
                <div className="popup" ref={this.props.childRef}>
                    {
                        (() => {
                            if (this.props.content != null){
                                const { title, description, desc_extend, link, coverImg, image } = this.props.content;
                                return (<div className="popContent">
                                    <div className="bgWrap">
                                        <div className="bgContent">
                                            <h1>{title}</h1>
                                            <p>{description}</p>
                                            {link != null && 
                                                (
                                                    <a className="btn" href={link} target="_blank" rel="noopener noreferrer">
                                                        Visit Website
                                                        <span className="icon">
                                                            <img src="images/linkout.png" alt="Visit Website"/>
                                                        <img src="images/linkout-hover.png" className="hover" alt="Visit Website" />
                                                        </span>
                                                    </a>
                                                )
                                            }
                                        </div>
                                        <div className="bgDiv">
                                            <img src={coverImg} alt={title} />
                                            <BgOverlay />
                                        </div>
                                    </div>
                                    <div className="showcase">
                                        <div className="descLong">
                                            <div className="title">
                                                <h2>About the Project</h2>
                                            </div>
                                            {desc_extend}
                                        </div>
                                        <div className="gallery">
                                            <div className="title">
                                                <h2>Gallery</h2>
                                            </div>

                                            {
                                                image.map((img, i) => {
                                                    return (
                                                        <div key={i} className="gallery-item">
                                                            <img src={img} alt={title} />
                                                        </div>
                                                    )
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                                )
                            }
                        })()  
                    }
                </div>
                <FullPageOverlay />
            </div>
        )
    }
}
